import {
	getEduPlan,
	getEduMarkList,
	getCalendarList,
	getCreditList,
	getPracticeList,
	getScheduleList
} from '@/api/declare.js'
import store from '@/store'
export default {
	data() {
		return {
			id: 0,
			planWeek: 24,
			planyear: "",
			planterm: "",
			model: {},
			markItem: [],
			calendarList: [],
			creditList: [],
			practiceList: [],
			scheduleList: []
		}
	},
	onLoad(option) {
		if (option.id != undefined) {
			this.id = option.id;
			this.getModel();
		}
	},
	methods: {
		getModel() {
			uni.showLoading({
				title: "加载中..."
			})
			getEduPlan({
				token_wx: store.getters.token,
				id: this.id
			}).then(res => {
				this.model = res.data
				this.planyear = res.data.year;
				this.planterm = this.planyear * 2;
				this.loadCalendar(); //学历表
				this.loadCreditList(); //学时、学分分配统计表
				this.loadPracticeList(); //实践教学
				this.loadScheduleList(); //指导性教学计划

				setTimeout(() => {
					uni.hideLoading()
				}, 1000)
			})
		},
		//========================学历表====================//
		loadCalendar() {
			this.loadCalendarList();
			this.getMarkList();
		},
		getMarkList() {
			getEduMarkList({
				token_wx: store.getters.token,
				p: 1,
				ps: 1000
			}).then(res => {
				this.markItem = res.data;
			})
		},
		loadCalendarList() {
			getCalendarList({
				token_wx: store.getters.token,
				pid: this.id
			}).then(res => {
				this.calendarList = res.data;
			})
		},
		getwplan(wplan) {
			var data = [];
			var wplanList = [];
			if (wplan != "" && wplan != null) {
				wplanList = JSON.parse(wplan);
			}
			for (var i = 0; i < this.planWeek; i++) {
				if (wplanList[i].sign == undefined) {
					data.push({
						sign: ""
					})
				} else {
					data.push({
						sign: wplanList[i].sign
					})
				}
			}

			return data;
		},
		//========================学历表====================//

		//========================获取学时、学分分配统计表====================//
		loadCreditList() {
			getCreditList({
				token_wx: store.getters.token,
				pid: this.id
			}).then(res => {
				res.data.forEach(item => {
					item.cexpList = this.getCexp(item.cexp)
				})
				this.creditList = res.data;

			})
		},
		getCexp(cexp) {
			var cexpList = [];
			if (cexp != "" && cexp != null) {
				cexpList = JSON.parse(cexp);
			}
			var data = [];
			for (var i = 1; i <= this.planyear; i++) {
				for (var j = 1; j <= 2; j++) {
					data.push({
						year: '第' + i + '学年',
						term: '第' + j + '学期',
						credit: cexpList[j].credit
					})
				}
			}
			return data;
		},
		//========================获取学时、学分分配统计表====================//
		//=======================实践教学===================================
		loadPracticeList() {
			getPracticeList({
				token_wx: store.getters.token,
				pid: this.id
			}).then(res => {
				this.practiceList = res.data;

			})
		},
		//=======================指导性教学计划===================================

		loadScheduleList() {
			getScheduleList({
				token_wx: store.getters.token,
				pid: this.id
			}).then(res => {
				res.data.forEach(item => {
					item.cexpList = this.getCexp(item.cexp)
				})
				this.scheduleList = res.data;
			})
		}
	}

}
